import React from "react";
const AboutUs = () => {
  return <div style={{marginBottom:"20px"}}>In general, employers take income tax from your pay and send it to the Canada Revenue Agency (CRA). If you earn additional income from: A rental property or renting out a portion of your home Investments (e.g. interest, dividends, capital gains) Being self-employed (e.g. a home business) Your pension more than one job, you must report these earnings on your tax return as well and send it to the CRA. If you paid too much income tax, you may be eligible for a tax refund. If you paid too little income tax, you may have to pay the CRA, in either a lump sum, or smaller amounts throughout the following year.</div>;
};

export default AboutUs;
